import {Indexable} from '../interfaces/indexable';

/**
 * Helper to get and set Attributes on Objects
 */
export class ObjectHelper {
  /**
   * Checking if the Element is an Object
   * @param obj
   */
  static isObject(obj: any): boolean {
    return obj != null && typeof (obj) === 'object';
  }

  /**
     * Getting Value from JSON-Object
     * @param {Indexable} object
     * @param {string} key
     * @return {any}
     */
  static get(object: Indexable, key: string) : any {
    try {
      const attributes: string[] = key.split('.');
      return this._iterateThrough(object, attributes);
    } catch (ex) {
      return undefined;
    }
  }

  /**
     * Iterating Through Object
     * @param {Indexable} obj
     * @param {string[]} attributes
     * @param {number} depth
     * @return {any}
     * @private
     */
  static _iterateThrough(obj: Indexable, attributes: string[], depth = 0): any {
    if (depth < 0) return undefined;

    while (attributes.length > depth) {
      const attribute :string = attributes.shift() as string;
      if (!obj) throw new Error('Unknown Key');
      obj = obj[attribute];
    }
    return obj;
  }

  /**
     * Setting value for Object
     * @param {Indexable} object
     * @param {string} key
     * @param {any} value
     */
  static set(object: Indexable, key: string, value: any): void {
    const attributes: string[] = key.split('.');
    object = this._iterateThrough(object, attributes, 1);
    const property = attributes[0];
    object[property] = value;
  }


  /**
     * Object to String
     * @param {Object} obj
     * @return {string}
     */
  static objectToStringDeep(obj: any) : string {
    if (!obj) return '';

    return Object.keys(obj).map((k: string) => {
      const value: any = obj[k];
      if (typeof (value) == 'object') {
        return ObjectHelper.objectToStringDeep(value);
      } else {
        return value;
      }
    }).toString();
  }
}
