
interface IWebComponentArgs {
  template: string | undefined;
  tag: string;
}

export function WebComponent(webComponentArgs: IWebComponentArgs) {
  /**
     * Defining Tag for HTML-Component
     * @param {any} constructor
     * @param {string} tagName
     */
  const defineTag = (constructor: any, tagName: string) => {
    if (!customElements.get(tagName)) {
      customElements.define(tagName, constructor);
    }
  };

  return function <T extends { new(...constructorArgs: any[]): void }>(constructorFunction: T) {
    /**
         * Building Wrapper Function for new Constructor
         * @param args
         */
    const newConstructorFunction: any = function (...args: any[]) {

      const func: any = function () {
        return new constructorFunction(...args);
      };
      func.prototype = constructorFunction.prototype;

      const result: any = new func();
      return result;
    };
    newConstructorFunction.prototype = constructorFunction.prototype;

    if (webComponentArgs.template) {
      Object.defineProperty(newConstructorFunction.prototype, 'template',
        {
          get: (): string => webComponentArgs.template || "",
        });
    }
    defineTag(constructorFunction, webComponentArgs.tag);
    return newConstructorFunction;
  };
}
