import {VetproviehElement} from './vetprovieh-element';
import {Indexable} from '../interfaces';
import {ViewHelper} from '../helpers';

/**
 * Repeats Template Element. Amount is set by the amount of objects
 * inside
 */
export class VetproviehRepeat extends VetproviehElement {
  /**
      * Getting View Template
      * @return {string}
      */
  static get template() {
    return VetproviehElement.template + `<div id="listElements"></div>`;
  }

  /**
       * Getting observed Attributes
       * @return {string[]}
       */
  static get observedAttributes() {
    return ['objects', 'orderBy'];
  }

    private _objects: Array<any> = [];
    private _listTemplate: DocumentFragment;
    private _orderBy = '+position';

    /**
     * Default-Contructor
     * @param {HTMLTemplateElement} pListTemplate
     */
    constructor(pListTemplate: HTMLTemplateElement | undefined = undefined) {
      super();
      console.log("REPEAT");

      const listTemplate = pListTemplate || this.querySelector('template');
      if (listTemplate) {
        this._listTemplate = listTemplate.content;
      } else {
        this._listTemplate = new DocumentFragment();
      }
    }

    /**
     * Get objects
     * @return {Array<any>}
     */
    public get objects(): Array<any> {
      return this._objects;
    }

    /**
     * Set objects
     * @param {Array<any>} v
     */
    public set objects(v: Array<any>) {
      if (this._objects != v) {
        this._objects = v;
        this.clearAndRender();
      }
    }

    /**
    * Get OrderBy
    * Expect "+position" for asceding positon
    * Expect "-position" for descending position
    * @return {string}
    */
    public get orderBy(): string {
      return this._orderBy;
    }

    /**
     * Set OrderBy
     * @param {string} v
     */
    public set orderBy(v: string) {
      if (this._orderBy != v) {
        this._orderBy = v;
        this.clearAndRender();
      }
    }

    /**
    * Connected Callback
    */
    connectedCallback() {
      this._initalizeShadowRoot(VetproviehRepeat.template);
      this.renderList();
    }

    /**
     * Clear and Render
     */
    clearAndRender() {
      this.clear();
      this._sortObjects();
      this.renderList();
    }

    /**
     * Sorting Objects
     */
    private _sortObjects() {
      try {
        const asc = this.orderBy.substring(0, 1) == '+' ? 1 : -1;
        const argument = this.orderBy.substring(1);
        this.objects = this.objects
            .sort((a: any, b: any) => {
              const aValue = a[argument];
              const bValue = b[argument];
              return (aValue - bValue) * asc;
            });
      } catch (e) {

      }
    }

    /**
     * List will be cleared
     */
    clear() {
      const list = this.list;
      if (list) list.innerHTML = '';
    }

    /**
     * Rendering List-Content
     */
    renderList() {
      this.objects
          .forEach((obj: any, index: number) => {
            this._attachToList(obj, index);
          });
    }

    /**
     * Inserts Element to List
     * @param {any} dataItem
     * @param {index} number
     * @private
     */
    protected _attachToList(dataItem: any, index = 0) {
      if (this.shadowRoot) {
        const newListItem: HTMLElement = this._generateListItem(dataItem);
        dataItem['index'] = index;
        ViewHelper.replacePlaceholders(newListItem, dataItem);

        const list = this.list;
        if (list) {
          list.appendChild(newListItem.children[0]);
        }
      }
    }


    /**
     * Getting List Element
     * @return {HTMLElement | undefined}
     */
    protected get list(): HTMLElement | undefined {
      if (this.shadowRoot) {
        return this.shadowRoot.getElementById('listElements') as HTMLElement;
      } else {
        return undefined;
      }
    }

    /**
    * Generate new Item for List which is based on the template
    * @param {any} dataItem
    * @param {boolean} activatedEventListener
    * @return {HTMLDivElement}
    * @private
    */
    protected _generateListItem(dataItem: any, activatedEventListener = false) {
      const newNode = document.importNode(this._listTemplate, true);
      const div = document.createElement('div');

      if (activatedEventListener) {
        div.addEventListener('click', () => {
          const selectedEvent = new Event('selected');
          (selectedEvent as Indexable)['data'] = dataItem;
          this.dispatchEvent(selectedEvent);
        });
      }

      div.appendChild(newNode);
      return div;
    }

    /**
     * Intializing Shadow-Root
     * @param {string} template
     * @protected
     */
    protected _initalizeShadowRoot(template: string) {
      // Lazy creation of shadowRoot.
      if (!this.shadowRoot) {
        super.attachShadow({
          mode: 'open',
        }).innerHTML = template;
      }
    }
}

if (!customElements.get('vp-repeat')) {
  customElements.define('vp-repeat', VetproviehRepeat);
}
