import {ObjectHelper} from './object-helper';
import {Indexable} from '../interfaces/indexable';

/**
 * Helpers for View
 */
export class ViewHelper {
  /**
     * Mark text yellow inside an element.
     * @param {Node} element
     * @param {string} input
     */
  static markElement(element: Node | ChildNode, input: string): void {
    if (input != '') {
      element.childNodes.forEach((n: Node) => {
        const value: string = n.nodeValue || '';
        if (n.nodeName === '#text' && value.indexOf(input) >= 0) {
          (element as HTMLElement).innerHTML = (n as Indexable)['data']
              .split(input)
              .join('<mark>' + input + '</mark>');
        } else {
          ViewHelper.markElement(n, input);
        }
      });
    }
  }

  /**
   * Getting URL-Parameter from address
   * @param {string} key
   * @return {string}
   */
  static getParameter(key: string): string | null {
    const urlString = window.location.href;
    const url = new URL(urlString);
    const value = url.searchParams.get(key);

    return value;
  }


  /**
     * Regex to fill keys in template
     * @return {RegExp}
     */
  static get regexTemplate(): RegExp {
    return /{{([a-zA-Z0-9\.]+)}}/;
  }

  /**
     * Replacing Placeholders in template from the loaded element
     * @param {HTMLElement} template
     * @param {Indexable} e
     */
  static replacePlaceholders(template: HTMLElement, e: Indexable): void {
    let match = null;
    while (match = template.innerHTML.match(ViewHelper.regexTemplate)) {
      let value = ObjectHelper.get(e, match[1]);
      value = value || '';
      template.innerHTML = template.innerHTML.replace(match[0], value);
    }
  }
}
