import {Indexable} from '../interfaces';

/**
 * BaseClass for view Elements
 */
export class VetproviehElement extends HTMLElement {
  public get template() {
    return '';
  }


  constructor(shadowed = true, render=true) {
    super();

    if (shadowed) {
      this.attachShadow({
        mode: 'open',
      });
    }

    if(render) this.render();
  }

  /**
     * Callback Implementation
     * @param {string} name
     * @param {any} old
     * @param {any} value
     */
  attributeChangedCallback(name: string, old: any, value: any) {
    if (old !== value) {
      this.sendCallback(`_${name}_beforeSet`, value);
      (this as Indexable)[name] = value;
      this.sendCallback(`_${name}_afterSet`, value);
      this.render();
    }
  }

  /**
   * Connected Callback
   */
  connectedCallback(){
  }


  private sendCallback(name: string, value: any) {
    const method = (this as Indexable)[name] as Function;
    if (method && typeof(method) === 'function') {
      (this as Indexable)[name](value);
    }
  }

  /**
   * Loading HTML-Element From ShadowRoot
   * @param {string} id
   * @return {HTMLElement | undefined}
   */
  getByIdFromShadowRoot(id: string): HTMLElement | undefined {
    if (this.shadowRoot) {
      return this.shadowRoot.getElementById(id) as HTMLElement;
    }
  }

  render() {
    const renderedTemplate = eval('`' + this.template + '`') as string;
    if (this.shadowRoot) {
      this.shadowRoot.innerHTML = renderedTemplate;
    } else {
      this.innerHTML = renderedTemplate;
    }
  }

  /**
     * Hide Or Show Element
     * @param {string} id
     * @param {boolean} show
     */
  updateVisibility(id: string, show: boolean) {
    const search = this.getByIdFromShadowRoot(id);
    if (search) {
      if (!show) {
        search.classList.add('is-hidden');
      } else {
        search.classList.remove('is-hidden');
      }
    }
  }

  public get innerHTML() {
    return super.innerHTML;
  }

  public set innerHTML(input: string) {
    if (this.shadowRoot != null) {
      this.shadowRoot.innerHTML = input;
    } else {
      super.innerHTML = input;
    }
  }


  // -----------------
  // CLASS METHODS
  // -----------------

  /**
     * Getting Template
     * @return {string}
     */
  static get template(): string {
    return `<link href="/node_modules/bulma/css/bulma.min.css" 
                  rel="stylesheet" type="text/css">`;
  }
}
